const URL = 'https://i.imgur.com/EpFWMQl.png';

export default function Product() {
	const [state_canvasVisible, set_canvasVisible] =
		React.useState<boolean>(false);
	const [state_text, set_text] = React.useState<string>(TESTTEXTL);
	const [state_origImageW, set_origImageW] = React.useState<number>(100);
	const [state_origImageH, set_origImageH] = React.useState<number>(100);
	const ref_stage = React.useRef<Konva.Stage>(null);
	const ref_image = React.useRef<Konva.Image>(null);

	const visibleCanvas = React.useCallback(() => set_canvasVisible(true), []);
	const invisibleCanvas = React.useCallback(() => {
		set_canvasVisible(false);
		console.log('invisibleCanvas');
	}, []);
	React.useEffect(() => {
		(async () => {
			await setOrigImageSize();
		})();
	}, []);
	const setOrigImageSize = async () => {
		try {
			const res = await loadImage(URL);
			set_origImageW(res.height);
			set_origImageH(res.width);
			console.log(['hyou!', res.width, res.height]);
		} catch (e) {
			console.log('hyou! onload error', e);
		}
	};

	const onTextChange = React.useCallback(
		(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
			set_text(e.target.value),
		[],
	);

	const save = () => {
		const uri = ref_stage.current?.toDataURL();
		uri && downloadURI(uri, 'stage.png');
	};
	const onTest = () => {
		ref_image.current?.moveToTop();
	};

	return (
		<div>
			<Box sx={{ flexGrow: 1 }}>
				<Grid container spacing={2}>
					<Grid item xs={12}>
						<Demo />
						<Button variant='contained' onClick={onTest}>
							test
						</Button>
						<Button variant='contained' onClick={visibleCanvas}>
							preview
						</Button>
						<Button variant='contained' onClick={save}>
							save
						</Button>
					</Grid>
				</Grid>
				<Grid container spacing={2}>
					<TextForm value={state_text} onChange={onTextChange} />
				</Grid>
			</Box>
			<Modal
				open={state_canvasVisible}
				onClose={invisibleCanvas}
				aria-labelledby='modal-modal-canvas'
				aria-describedby='modal-modal-canvas'
				keepMounted
			>
				<Box
					sx={{
						position: 'absolute' as 'absolute',
						top: '50%',
						left: '50%',
						transform: 'translate(-50%, -50%)',
						bgcolor: 'background.paper',
						//border: '2px solid #000',
						boxShadow: 24,
						p: 0,
						overflow: 'auto',
						maxHeight: '80vh',
						maxWidth: '80vw',
					}}
				>
					<Canvas
						text={state_text}
						imageUrl={URL}
						origImageH={state_origImageH}
						origImageW={state_origImageW}
						ref_stage={ref_stage}
						ref_image={ref_image}
					/>
				</Box>
			</Modal>
			<p>!!!!!!!!!!!!!!!!!</p>
		</div>
	);
}

const ModalContainer = (props: { children: React.ReactNode }) => {
	return (
		<div>
			<p>awaaa</p>
			{props.children}
		</div>
	);
};

const Demo = () => {
	const { width, height } = useWindowSize();

	return (
		<div>
			<div>width: {width}</div>
			<div>height: {height}</div>
		</div>
	);
};
const loadImage = (src: string): Promise<HTMLImageElement> => {
	return new Promise((resolve, reject) => {
		const img = new Image();
		img.onload = () => resolve(img);
		img.onerror = (e) => reject(e);
		img.src = src;
	});
};

function downloadURI(uri: string, name: string) {
	var link = document.createElement('a');
	link.download = name;
	link.href = uri;
	document.body.appendChild(link);
	link.click();
	document.body.removeChild(link);
}
